<template>
<div class="app-container">
  <!-- Conditional Header -->
  <header class="dashboard-header">
    <div class="logo-greeting">
      <div class="logo">
        <img src="@/assets/lms-Logo.png" alt="LMS Logo" />
      </div>
      <div class="greeting">
        <h1>Welcome, {{ userName }}</h1>
        <p v-if="showHeader">Glad to see you again, ready for another session?</p>
        <p v-else>Great to have you back, ready to inspire and guide the next generation?</p>
      </div>
    </div>
    <nav v-if="showHeader" class="nav-buttons">
      <button @click="navigateTo('/overall')">Overall</button>
      <button @click="navigateTo('/performance')">Performance</button>
      <button @click="navigateTo('/engagement')">Engagement</button>
      <button @click="navigateTo('/admin-dashboard')">Admin</button>
      <button @click="navigateTo('/educator-dashboard')">Educator</button>
    </nav>
  </header>

  <!-- Dynamic Content -->
  <main class="main-content">
    <router-view />
  </main>
</div>
</template>

<script>
import { jwtDecode } from 'jwt-decode'

// Rollen-URIs basierend auf deinem Token
const LTI_ROLES = {
  ADMIN_INSTITUTION: 'http://purl.imsglobal.org/vocab/lis/v2/institution/person#Administrator',
  ADMIN_SYSTEM: 'http://purl.imsglobal.org/vocab/lis/v2/system/person#Administrator',
  TEACHER: 'http://purl.imsglobal.org/vocab/lis/v2/membership#Instructor',
  STUDENT: 'http://purl.imsglobal.org/vocab/lis/v2/membership#Learner'
}

export default {
  name: 'App',
  data() {
    return {
      userName: 'User',
      roles: []
    }
  },
  computed: {
    showHeader() {
      console.log('[Route Check] Current path:', this.$route.path)
      return this.$route.path !== '/educator-dashboard'
    },
    isStudent() {
      const hasRole = this.roles.some(role => role === LTI_ROLES.STUDENT)
      console.log('[Role] Student check:', hasRole)
      return hasRole
    },
    isTeacher() {
      const hasRole = this.roles.some(role => role === LTI_ROLES.TEACHER)
      console.log('[Role] Teacher check:', hasRole)
      return hasRole
    },
    isAdmin() {
      const hasRole = this.roles.some(role => 
        role === LTI_ROLES.ADMIN_INSTITUTION ||
        role === LTI_ROLES.ADMIN_SYSTEM
      )
      console.log('[Role] Admin check:', hasRole)
      return hasRole
    }
  },
  methods: {
    navigateTo(route) {
      console.log('[Navigation] Navigating to:', route)
      this.$router.push(route)
    },
    getCookie(name) {
      const value = `; ${document.cookie}`
      const parts = value.split(`; ${name}=`)
      const cookie = parts.length === 2 ? parts.pop().split(';').shift() : null
      console.log('[Cookie] Found auth_token:', !!cookie)
      return cookie
    },
    fetchUserData() {
      console.group('[Auth] Starting authentication check')
      const token = this.getCookie('auth_token')
      
      if (!token) {
        console.error('[Auth] No token found in cookies')
        this.$router.push('/login')
        console.groupEnd()
        return
      }

      try {
        console.log('[Auth] Decoding token...')
        const decoded = jwtDecode(token)
        console.log('[Auth] Decoded token structure:', decoded)

        // Proxy-Problem lösen und Rollen extrahieren
        this.roles = JSON.parse(JSON.stringify(
          decoded['https://purl.imsglobal.org/spec/lti/claim/roles'] || []
        ))
        console.log('[Auth] User roles:', this.roles)

        // Benutzernamen setzen
        this.userName = decoded.name || decoded.given_name || 'Unknown User'
        console.log('[Auth] Username set to:', this.userName)

        // Automatische Weiterleitung
        this.autoRedirect()

      } catch (error) {
        console.error('[Auth] Token decoding failed:', error)
        this.$router.push('/login')
      }
      
      console.groupEnd()
    },
    autoRedirect() {
      console.log('[Routing] Current path:', this.$route.path)
      
      if (this.isAdmin) {
        console.log('[Routing] Admin detected')
        if (!this.$route.path.startsWith('/admin')) {
          console.log('[Routing] Redirecting to admin dashboard')
          this.$router.push('/admin-dashboard')
        }
        return
      }

      if (this.isTeacher) {
        console.log('[Routing] Teacher detected')
        if (!this.$route.path.startsWith('/educator')) {
          console.log('[Routing] Redirecting to educator dashboard')
          this.$router.push('/educator-dashboard')
        }
        return
      }

      if (this.isStudent) {
        console.log('[Routing] Student detected')
        if (!['/overall', '/performance', '/engagement'].includes(this.$route.path)) {
          console.log('[Routing] Redirecting to overall')
          this.$router.push('/overall')
        }
      }
    }
  },
  created() {
    console.log('[App] Component initialized')
    this.fetchUserData()
  },
  watch: {
    $route(to) {
      console.log('[Routing] Route changed to:', to.path)
    }
  }
}
</script>

